
:root {
    /* variables */
    --box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
h1 {
    font-size: 1.8em;
    margin: 0 0 .5em 0;
}
h2 {
	font-weight: 600;
	font-style: italic;
}
img.me {
  height: 120px;
  border-radius: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  margin-top: 214px;
  -webkit-box-shadow: var(--box-shadow);
          box-shadow: var(--box-shadow);
}
#inner {
	height: 400px;
	width: 400px;
	padding: 12px;
	position: absolute;
	top: 45%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
	/* background-color: #eee; */
	background-image: url(/static/img/concrete-texture.3eb73e5.png);
	opacity: .8;
	overflow: auto;
	border-radius: 4px;
    -webkit-box-shadow: var(--box-shadow);
            box-shadow: var(--box-shadow);
}
#wrapper {
	height: 100%;
}
#buttons {
	position: absolute;
	top: 43%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
	margin-left: -270px;
}
#buttons ul {
	list-style: none;
}
#buttons li {
	margin: 1em 0;
}
button {
	color: rgb(247, 210, 210);
	font-size: 20px;
	font-weight: 500;
	width: 60px;
	height: 60px;
	border-radius: 60px;
	border: none;
	cursor: pointer;
	-webkit-transition: background 3s;
	transition: background 3s;
    -webkit-box-shadow: var(--box-shadow);
            box-shadow: var(--box-shadow);
}
button:focus {
	outline: none;
}
footer {
    position: absolute;
    top: 50%;
    left: 50%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
    margin-top: 320px;
    font-size: 12px;
}
@media screen and (max-width: 640px) {
#buttons {
        width: 100%;
        top: 7%;
        margin-left: 0;
}
#buttons ul {
        padding: 0;
        text-align: center;
}
#buttons li {
        display: inline;
}
#inner {
        width: 85%;
}
}




















































































































































































